//
import vue from 'vue';
import vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';
vue.use(vuex);

const store =new vuex.Store({
    //
    state:{
          //
    topics:[
        {name:'all',desc:''},
        {name:'good',desc:''},
        {name:'share',desc:''},
        {name:'ask',desc:''},
        {name:'job',desc:''},
        {name:'dev',desc:''}
      ],
      //
      loginname:'',
      //
      unLoginMoreMenus:{login:''},
      //
      loginMoreMenus:{center:'',create:'',message:'',logout:''},
      //
      isShowMore:false,
      //
      isLogin:false,
      //accesstoken
      accesstoken:'',
      //id
      author_id:''
    },
    //
    getters:{
      moreMenus:function(state){
        if(state.isLogin){
          return state.loginMoreMenus;
        }else{
          return state.unLoginMoreMenus;
        }
      },
      getTopicDescByTab:function(state){
        return function (tab){
          for(var index=0;index<state.topics.length;index++){
            if(state.topics[index].name==tab){
              return state.topics[index].desc
            }
          }
        }
      }
    },
    //mutations  actions
    mutations,
    actions
});

export default store;


